import React, { useEffect, useState } from 'react';
import { sendMessage } from './api/messages';
import './App.css';
import { useReader } from './useReader';
import { SttSettings, useStt } from './useStt';
import VoiceAssistantSphere, { VoiceAssistantStatus } from './VoiceAssistantSphere/VoiceAssistantSphere';
import { MessagesList } from './MessagesList/MessagesList';
import { useMessages } from './MessagesList/useMessages';
import { Message } from '@project/model';
import styled from 'styled-components';

const AppContainerStyled = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 5%;
`;

function App() {
  const [chatAnswer, setChatAnswer] = useState('');
  const [assistantStatus, setAssistantStatus] = useState<VoiceAssistantStatus>('waiting');
  const [isLoading, setIsLoading] = useState(false);

  const {
    pushMessage,
    messages,
    clearMessages
  } = useMessages();

  useEffect(() => {
    if (isLoading) {
      setAssistantStatus('loading');
    } else {
      setAssistantStatus('waiting');
    }
  }, [isLoading]);

  const { read } = useReader({
    onReadStart: () => setAssistantStatus('voicing'),
    onReadFinished: () => setAssistantStatus('waiting')
  });

  const sendHandler = (message: string) => {
    (async () => {
      if (!message) {
        return;
      }
      setIsLoading(true);
      const newUserMessage = { role: 'user', content: message } as Message;

      pushMessage(newUserMessage);

      const resp = await sendMessage([newUserMessage]);

      setIsLoading(false);
      const answerMessage = resp.choices[0].message as Message;
      read(answerMessage.content);

      pushMessage(answerMessage);
    })();
  }

  const sttSettings: SttSettings = {
    onSpeechFinished: sendHandler
  };

  const {
    speechStatus,
    startSpeechHandler,
    stopSpeechHandler,
  } = useStt(sttSettings);

  const assistantSphereOnClick = () => {
    if (speechStatus === 'active') {
      stopSpeechHandler();
    } else {
      startSpeechHandler();
    }
  }

  useEffect(() => {
    let newAssistantStatus:VoiceAssistantStatus = 'waiting';

    switch (speechStatus) {
      case 'error':
        newAssistantStatus = 'error';
        break;
      case 'active':
        newAssistantStatus = 'listening';
        break;
      case 'stopped':
        newAssistantStatus = 'waiting';
    }

    if (!isLoading) {
      setAssistantStatus(newAssistantStatus);
    }
  }, [speechStatus]);
  
  return (
    <AppContainerStyled>
        <MessagesList messages={messages} />
        <VoiceAssistantSphere
          onClick={assistantSphereOnClick}
          status={assistantStatus}
        />
    </AppContainerStyled>
  );
}

export default App;
