import * as React from 'react';
import { MutableRefObject, useEffect, useRef } from 'react';
import { BlobColors, useThreeBlob } from './useThreeBlob';
import styled from 'styled-components';

export type VoiceAssistantStatus =
  | 'listening'
  | 'waiting'
  | 'loading'
  | 'voicing'
  | 'error';

interface IBlobProps {
  status: VoiceAssistantStatus,
  onClick: () => void;
}

const VoiceSphereWrapperStyled = styled.div`
  display: inline-flex;
`;

const VoiceAssistantSphere = (props: IBlobProps) => {
  const { status, onClick } = props;

  const canvasRef = useRef<HTMLCanvasElement>(null) as MutableRefObject<HTMLCanvasElement>;

  const {
    changeColor,
    setSpeed,
    setSpikes,
    setProcessing
  } = useThreeBlob(canvasRef);

  useEffect(() => {
    let color: BlobColors = 'white';
    let speed = 75;
    let spikes = .7;
    let processing = 1;

    switch (status) {
      case 'voicing':
        color = 'green';
        break;
      case 'loading':
        color = 'orange';
        break;
      case 'waiting':
        color = 'white';
        break;
      case 'error':
        color = 'red';
        break;
      case 'listening':
        color = 'blue';
    }

    changeColor(color);
    setSpikes(spikes);
    setSpeed(speed);
    setProcessing(processing);
  }, [status]);

  return (
    <VoiceSphereWrapperStyled onClick={onClick}>
      <canvas
        ref={canvasRef}
        style={{ width: '250px', height: '250px' }}
      />
    </VoiceSphereWrapperStyled>
  );
}

export default VoiceAssistantSphere;