import { useEffect, useRef, useState } from 'react';

export type SttSettings = {
    onSpeechFinished?: (text: string) => void;
    onSpeechStart?: () => void;
}

export function useStt(sttSettings: SttSettings) {
    const {
      onSpeechFinished,
      onSpeechStart
    } = sttSettings;

    const autoStopTimerRef = useRef<ReturnType<typeof setTimeout> | null>(null);
    const [speechIsAvailable, setSpeechIsAvailable] = useState(true);
    const SpeechRecognitionRef = useRef<any>(null);
    const [fullTranscription, setFullTranscription] = useState('');
    const [tmpTranscription, setTmpTranscription] = useState('');
    const [speechStatus, setSpeechStatus] = useState<'active' | 'error' | 'stopped'>('stopped');

    const fullTranscriptionRef = useRef('');

    useEffect(() => {
        fullTranscriptionRef.current = fullTranscription;
    }, [fullTranscription]);

    const startSpeechHandler = () => {
        if (SpeechRecognitionRef) {
            SpeechRecognitionRef.current.start();
            setFullTranscription('');
        }
    }

    const stopSpeechHandler = () => {
        if (SpeechRecognitionRef) {
            SpeechRecognitionRef.current.stop();
            if (autoStopTimerRef.current) {
                clearTimeout(autoStopTimerRef.current as unknown as number);
                autoStopTimerRef.current = null;
            }
        }
    }

    const resetFullTranscription = () => setFullTranscription('');

    const resetTmpTranscription = () => setTmpTranscription('');

    useEffect(() => {
        if ("webkitSpeechRecognition" in window) {
            // Initialize webkitSpeechRecognition
            SpeechRecognitionRef.current = new webkitSpeechRecognition();

            // Set the properties for the Speech Recognition object
            SpeechRecognitionRef.current.continuous = true;
            SpeechRecognitionRef.current.interimResults = true;
            SpeechRecognitionRef.current.lang = 'ru-RU';

            // Callback Function for the onStart Event
            SpeechRecognitionRef.current.onstart = () => {
                // Show the Status Element
                setSpeechStatus('active');
            };
            SpeechRecognitionRef.current.onerror = () => {
                // Hide the Status Element
                setSpeechStatus('error');
            };
            SpeechRecognitionRef.current.onend = () => {
                // Hide the Status Element
                setSpeechStatus('stopped');
                if (onSpeechFinished) {
                    onSpeechFinished(fullTranscriptionRef.current);
                }
            };

            SpeechRecognitionRef.current.onresult = (event: any) => {
                // TODO: Fix mobile autostop: https://stackoverflow.com/questions/57778587/javascript-speech-recognition-api-not-continuous-on-mobile-device

                // Create the interim transcript string locally because we don't want it to persist like final transcript

                // Loop through the results from the speech recognition object.
                for (let i = event.resultIndex; i < event.results.length; ++i) {
                    // If the result item is Final, add it to Final Transcript, Else add it to Interim transcript
                    if (event.results[i].isFinal) {
                        setFullTranscription(event.results[i][0].transcript);
                    } else {
                        setTmpTranscription(event.results[i][0].transcript);
                    }
                }
            };
        } else {
            console.log("Speech Recognition Not Available");
            setSpeechIsAvailable(false);
        }
    }, []);

    useEffect(() => {
        if (autoStopTimerRef.current) {
            clearTimeout(autoStopTimerRef.current as unknown as number);
        }
        if (tmpTranscription) {
            autoStopTimerRef.current = setTimeout(() => {
                stopSpeechHandler();
            }, 2000);
        }
    }, [tmpTranscription]);

    useEffect(() => {
        console.log(fullTranscription)
    }, [fullTranscription]);

    return {
        fullTranscription,
        tmpTranscription,
        speechStatus,
        speechIsAvailable,
        startSpeechHandler,
        stopSpeechHandler,
        resetFullTranscription,
        resetTmpTranscription
    };
}