import { useState } from 'react';
import { Message } from '@project/model';


export const useMessages = () => {
  const [messages, setMessages] = useState<Message[]>([]);

  const pushMessage = (msg: Message) => {
    setMessages(p => [...p, msg]);
  }

  const clearMessages = () => {
    setMessages([]);
  }

  return {
    messages,
    pushMessage,
    clearMessages
  }
}