import * as React from 'react';
import { Message } from '@project/model';
import styled from 'styled-components';
import { useEffect, useRef } from 'react';

const MessagesListStyled = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 60vw;
`;

type MessageItemStyledProps = {
  role: Message['role'];
}

const MessageItemStyled = styled.div<MessageItemStyledProps>`
  padding: 10px;
  align-self: flex-${({role}) => role === 'user' ? 'start' : 'end'};
  max-width: 90%;
  margin-top: 10px;
  background: ${({role}) => role === 'user' ? 'aliceblue' : '#78A0D8FF'};
  border-radius: 5px;
`;

interface IMessagesListProps {
  messages: Message[]
}

export const MessagesList = (props: IMessagesListProps) => {
  const {messages} = props;

  const MessageListRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const comp = MessageListRef.current;
    if (!comp) {
      return;
    }

    comp.scrollTo({ top: comp.offsetHeight });
  }, [messages]);

  return (
    <MessagesListStyled ref={MessageListRef} >
      {messages.map(message => (
        <MessageItemStyled role={message.role}>
          {message.content}
        </MessageItemStyled>
      ))}
    </MessagesListStyled>
  );
}
