// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck

import { MutableRefObject, useEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import SimplexNoise from 'simplex-noise';

  const colors = {
    green: 0x339933,
    white: 0x798296,
    red: 0xff0033,
    orange: 0xff9900,
    blue: 0x0033ff
  };

export type BlobColors = keyof typeof colors;

export const useThreeBlob = (canvasRef: MutableRefObject<HTMLCanvasElement>) => {
  const speedRef = useRef(75);
  const spikesRef = useRef(.7);
  const processingRef = useRef(1);

  const [currentColor, setCurrentColor] = useState<BlobColors>('blue');
  const [speed, setSpeed] = useState(75);
  const [spikes, setSpikes] = useState(.7);
  const [processing, setProcessing] = useState(1);

  useEffect(() => {
    speedRef.current = speed;
  }, [speed]);
  useEffect(() => {
    spikesRef.current = spikes;
  }, [spikes]);
  useEffect(() => {
    processingRef.current = processing;
  }, [processing]);

  const ambientLightRef = useRef<THREE.AmbientLight>(null);

  useEffect(() => {
    if (ambientLightRef.current) {
      ambientLightRef.current.color = new THREE.Color(colors[currentColor]);
    }
  }, [currentColor]);

  const changeColor = (color: BlobColors) => {
    setCurrentColor(color);
  }


  useEffect(() => {

    let canvas = canvasRef.current;

    let renderer = new THREE.WebGLRenderer({
      canvas: canvas,
      context: canvas.getContext('webgl2')!,
      antialias: true,
      alpha: true
    });
    let simplex = new SimplexNoise();

    renderer.setSize(canvas.offsetWidth, canvas.offsetHeight);
    renderer.setPixelRatio(window.devicePixelRatio || 1);

    let scene = new THREE.Scene();
    let camera = new THREE.PerspectiveCamera(45, canvas.offsetWidth / canvas.offsetHeight, 0.1, 1000);

    camera.position.z = 5;

    let geometry = new THREE.SphereGeometry(.8, 128, 128);

    let material = new THREE.MeshPhongMaterial({
      color: 0xE4ECFA,
      shininess: 100
    });

    let lightTop = new THREE.DirectionalLight(0xFFFFFF, .7);
    lightTop.position.set(0, 500, 200);
    lightTop.castShadow = true;
    scene.add(lightTop);

    let lightBottom = new THREE.DirectionalLight(0xFFFFFF, .25);
    lightBottom.position.set(0, -500, 400);
    lightBottom.castShadow = true;
    scene.add(lightBottom);



    let ambientLight = new THREE.AmbientLight(colors[currentColor]);
    ambientLightRef.current = ambientLight;
    scene.add(ambientLight);

    let sphere = new THREE.Mesh(geometry, material);

    scene.add(sphere);

    let update = () => {

      let time = performance.now() * 0.00001 * speedRef.current * Math.pow(processingRef.current, 3),
        spikes = spikesRef.current * processingRef.current;

      for(let i = 0; i < sphere.geometry.vertices.length; i++) {
        let p = sphere.geometry.vertices[i];
        p.normalize().multiplyScalar(1 + 0.3 * simplex.noise3D(p.x * spikes, p.y * spikes, p.z * spikes + time));
      }

      sphere.geometry.computeVertexNormals();
      sphere.geometry.normalsNeedUpdate = true;
      sphere.geometry.verticesNeedUpdate = true;

    }

    function animate() {
      setTimeout(() => {
        update();
        renderer.render(scene, camera);
        requestAnimationFrame(animate);
      }, 17);
    }

    requestAnimationFrame(animate);
  }, []);

  return {
    currentColor,
    changeColor,
    spikes, setSpikes,
    speed, setSpeed,
    processing, setProcessing
  };
}