import * as React from 'react';
import { useEffect, useRef } from 'react';

interface IReaderProps {
  onReadStart: () => void;
  onReadFinished: () => void;
}

export const useReader = (props: IReaderProps) => {
  const {  onReadStart, onReadFinished } = props;
  const SpeechSynthesisRef = useRef(new SpeechSynthesisUtterance());

  useEffect(() => {
    SpeechSynthesisRef.current.lang = "ru";
    SpeechSynthesisRef.current.addEventListener('end', () => {
      onReadFinished();
    });
    SpeechSynthesisRef.current.addEventListener('start', () => {
      onReadStart();
    });
    SpeechSynthesisRef.current.addEventListener("boundary", (event) => {
      // TODO: Implement reactions on boundary (visualisation)
      console.log(event);
    });
    SpeechSynthesisRef.current.voice = window.speechSynthesis.getVoices()[0];
  }, []);
  const read = (text: string) => {
    if (text) {
      alert('Got text');
      SpeechSynthesisRef.current.text = text;
      window.speechSynthesis.speak(SpeechSynthesisRef.current);
    }
  }

  return {
    read
  }
}
